.title::before {
	display: inline-block;
	margin-top: 0px;
	vertical-align: middle;
	content: '';
	background-color: #c2c2c2;
	width: 40px;
	height: 2px;
}

.title::after {
	display: inline-block;
	margin-top: 0px;
	vertical-align: middle;
	content: '';
	background-color: #c2c2c2;
	width: 40px;
	height: 2px;
}

a:hover {
	text-decoration: none;
}
